/* ============================================
   ðŸŽ¨ MODERN UNIQUE CHARACTER
   Eye-Catching, Creative, Unforgettable
   ============================================ */

/* ============================================
   ðŸŽ¯ CHARACTER CONTAINER
   ============================================ */
   .character-container {
    position: relative;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    perspective: 1000px;
    animation: characterFloat 4s ease-in-out infinite;
  }
  
  @keyframes characterFloat {
    0%, 100% {
      transform: translateY(0) rotateY(0deg);
    }
    50% {
      transform: translateY(-15px) rotateY(180deg);
    }
  }
  
  /* ============================================
     ðŸŒŸ HOLOGRAPHIC HEAD
     ============================================ */
  .holo-head {
    position: relative;
    width: 200px;
    height: 200px;
    margin: 0 auto;
    transform-style: preserve-3d;
    animation: headRotate 6s linear infinite;
  }
  
  @keyframes headRotate {
    from {
      transform: rotateY(0deg);
    }
    to {
      transform: rotateY(360deg);
    }
  }
  
  /* Glowing orb head */
  .head-orb {
    position: absolute;
    width: 100%;
    height: 100%;
    background: radial-gradient(
      circle at 30% 30%,
      rgba(138, 43, 226, 0.9) 0%,
      rgba(30, 144, 255, 0.7) 50%,
      rgba(0, 255, 255, 0.5) 100%
    );
    border-radius: 50%;
    box-shadow: 
      0 0 50px rgba(138, 43, 226, 0.8),
      0 0 100px rgba(30, 144, 255, 0.6),
      inset 0 0 30px rgba(255, 255, 255, 0.5);
    animation: orbPulse 2s ease-in-out infinite;
  }
  
  @keyframes orbPulse {
    0%, 100% {
      transform: scale(1);
      box-shadow: 
        0 0 50px rgba(138, 43, 226, 0.8),
        0 0 100px rgba(30, 144, 255, 0.6),
        inset 0 0 30px rgba(255, 255, 255, 0.5);
    }
    50% {
      transform: scale(1.1);
      box-shadow: 
        0 0 80px rgba(138, 43, 226, 1),
        0 0 150px rgba(30, 144, 255, 0.8),
        inset 0 0 50px rgba(255, 255, 255, 0.8);
    }
  }
  
  /* ============================================
     ðŸ‘ CYBERNETIC EYES
     ============================================ */
  .cyber-eyes {
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 40px;
    animation: eyesScan 3s ease-in-out infinite;
  }
  
  @keyframes eyesScan {
    0%, 100% {
      transform: translateX(-50%) translateY(0);
    }
    50% {
      transform: translateX(-50%) translateY(-5px);
    }
  }
  
  .cyber-eye {
    position: relative;
    width: 60px;
    height: 30px;
    background: linear-gradient(135deg, #1a1a2e, #0f0f23);
    border-radius: 15px;
    border: 2px solid #00ffff;
    overflow: hidden;
    box-shadow: 
      0 0 20px rgba(0, 255, 255, 0.8),
      inset 0 0 10px rgba(0, 255, 255, 0.3);
  }
  
  .eye-scanner {
    position: absolute;
    top: 50%;
    left: -10px;
    width: 80px;
    height: 2px;
    background: linear-gradient(90deg, transparent, #00ffff, transparent);
    transform: translateY(-50%);
    animation: scannerMove 2s linear infinite;
  }
  
  @keyframes scannerMove {
    from {
      left: -10px;
    }
    to {
      left: -10px;
      width: 80px;
    }
    50% {
      width: 100px;
    }
  }
  
  /* ============================================
     ðŸŽ¯ HOLOFACE
     ============================================ */
  .holo-face {
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translateX(-50%);
    width: 120px;
    height: 80px;
    background: linear-gradient(135deg, 
      rgba(0, 255, 255, 0.1) 0%,
      rgba(138, 43, 226, 0.1) 100%
    );
    border-radius: 0 0 50px 50px;
    border: 1px solid rgba(0, 255, 255, 0.3);
    animation: faceMorph 4s ease-in-out infinite;
  }
  
  @keyframes faceMorph {
    0%, 100% {
      border-radius: 0 0 50px 50px;
      height: 80px;
    }
    25% {
      border-radius: 0 0 60px 60px;
      height: 60px;
    }
    50% {
      border-radius: 0 0 40px 40px;
      height: 100px;
    }
    75% {
      border-radius: 0 0 45px 45px;
      height: 70px;
    }
  }
  
  /* ============================================
     ðŸŒŠ QUANTUM BODY
     ============================================ */
  .quantum-body {
    position: relative;
    width: 180px;
    height: 200px;
    margin: 20px auto 0;
    background: linear-gradient(135deg, 
      rgba(138, 43, 226, 0.8) 0%,
      rgba(30, 144, 255, 0.6) 50%,
      rgba(0, 255, 255, 0.4) 100%
    );
    border-radius: 20px;
    box-shadow: 
      0 20px 40px rgba(138, 43, 226, 0.4),
      inset 0 -10px 20px rgba(255, 255, 255, 0.2);
    animation: bodyShift 5s ease-in-out infinite;
    position: relative;
    overflow: hidden;
  }
  
  @keyframes bodyShift {
    0%, 100% {
      transform: translateY(0) scale(1);
    }
    50% {
      transform: translateY(-10px) scale(1.05);
    }
  }
  
  /* Quantum particles inside body */
  .quantum-particles {
    position: absolute;
    inset: 0;
    overflow: hidden;
  }
  
  .particle {
    position: absolute;
    width: 4px;
    height: 4px;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 50%;
    animation: particleFloat 3s ease-in-out infinite;
  }
  
  .particle:nth-child(1) {
    top: 20%;
    left: 20%;
    animation-delay: 0s;
  }
  
  .particle:nth-child(2) {
    top: 60%;
    left: 70%;
    animation-delay: 0.5s;
  }
  
  .particle:nth-child(3) {
    top: 80%;
    left: 40%;
    animation-delay: 1s;
  }
  
  .particle:nth-child(4) {
    top: 40%;
    left: 80%;
    animation-delay: 1.5s;
  }
  
  .particle:nth-child(5) {
    top: 70%;
    left: 30%;
    animation-delay: 2s;
  }
  
  @keyframes particleFloat {
    0%, 100% {
      transform: translateY(0) scale(1);
      opacity: 0.8;
    }
    50% {
      transform: translateY(-20px) scale(1.5);
      opacity: 1;
    }
  }
  
  /* ============================================
     ðŸŽª FLOATING ELEMENTS
     ============================================ */
  .floating-elements {
    position: absolute;
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
  
  .floating-orb {
    position: absolute;
    width: 20px;
    height: 20px;
    background: radial-gradient(circle, #00ffff, #0080ff);
    border-radius: 50%;
    box-shadow: 0 0 20px rgba(0, 255, 255, 0.8);
  }
  
  .floating-orb:nth-child(1) {
    top: -50px;
    left: 10%;
    animation: orbit1 8s linear infinite;
  }
  
  .floating-orb:nth-child(2) {
    top: -30px;
    right: 10%;
    animation: orbit2 10s linear infinite;
  }
  
  .floating-orb:nth-child(3) {
    bottom: -40px;
    left: 20%;
    animation: orbit3 12s linear infinite;
  }
  
  .floating-orb:nth-child(4) {
    bottom: -30px;
    right: 20%;
    animation: orbit4 9s linear infinite;
  }
  
  @keyframes orbit1 {
    from {
      transform: rotate(0deg) translateX(50px);
    }
    to {
      transform: rotate(360deg) translateX(50px);
    }
  }
  
  @keyframes orbit2 {
    from {
      transform: rotate(0deg) translateX(-50px);
    }
    to {
      transform: rotate(-360deg) translateX(-50px);
    }
  }
  
  @keyframes orbit3 {
    from {
      transform: rotate(0deg) translateY(30px);
    }
    to {
      transform: rotate(360deg) translateY(30px);
    }
  }
  
  @keyframes orbit4 {
    from {
      transform: rotate(0deg) translateY(-30px);
    }
    to {
      transform: rotate(-360deg) translateY(-30px);
    }
  }
  
  /* ============================================
     ðŸŒŸ ENERGY FIELD
     ============================================ */
  .energy-field {
    position: absolute;
    inset: -50px;
    background: radial-gradient(
      circle at center,
      rgba(138, 43, 226, 0.1) 0%,
      rgba(30, 144, 255, 0.05) 50%,
      transparent 100%
    );
    border-radius: 50%;
    animation: energyPulse 3s ease-in-out infinite;
    pointer-events: none;
  }
  
  @keyframes energyPulse {
    0%, 100% {
      transform: scale(1);
      opacity: 0.5;
    }
    50% {
      transform: scale(1.2);
      opacity: 0.8;
    }
  }
  
  /* ============================================
     ðŸŽ¨ INTERACTIVE STATES
     ============================================ */
  .character-container:hover .holo-head {
    animation-play-state: paused;
    transform: scale(1.2);
  }
  
  .character-container:hover .cyber-eyes {
    animation-play-state: paused;
  }
  
  .character-container:hover .cyber-eye {
    background: linear-gradient(135deg, #00ffff, #ff00ff);
    box-shadow: 
      0 0 30px rgba(255, 0, 255, 1),
      inset 0 0 15px rgba(255, 0, 255, 0.5);
  }
  
  .character-container:hover .quantum-body {
    background: linear-gradient(135deg, 
      rgba(255, 0, 255, 0.8) 0%,
      rgba(0, 255, 255, 0.6) 50%,
      rgba(138, 43, 226, 0.4) 100%
    );
    transform: scale(1.1);
  }
  
  /* ============================================
     ðŸŽª ENTRANCE ANIMATION
     ============================================ */
  .character-container {
    animation: characterEntrance 1s cubic-bezier(0.34, 1.56, 0.64, 1) backwards;
  }
  
  @keyframes characterEntrance {
    from {
      opacity: 0;
      transform: scale(0) rotateY(180deg);
      filter: blur(20px);
    }
    to {
      opacity: 1;
      transform: scale(1) rotateY(0deg);
      filter: blur(0);
    }
  }
  
  /* ============================================
     ðŸŽ¨ RESPONSIVE DESIGN
     ============================================ */
  @media (max-width: 768px) {
    .holo-head {
      width: 150px;
      height: 150px;
    }
    
    .cyber-eye {
      width: 40px;
      height: 20px;
    }
    
    .quantum-body {
      width: 140px;
      height: 160px;
    }
    
    .floating-orb {
      width: 15px;
      height: 15px;
    }
  }
  
  /* ============================================
     ðŸŽ¨ ACCESSIBILITY
     ============================================ */
  @media (prefers-reduced-motion: reduce) {
    .character-container,
    .holo-head,
    .cyber-eyes,
    .quantum-body,
    .floating-orb {
      animation: none;
    }
  }
  
  /* ============================================
     ðŸŽ¨ PERFORMANCE OPTIMIZATIONS
     ============================================ */
  .character-container,
  .holo-head,
  .cyber-eyes,
  .quantum-body,
  .floating-orb,
  .particle {
    will-change: transform;
  }

  


  